.RoomList {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
  margin-bottom: 80px;

  .no-room {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform: translateY(calc(38vh - 250px));

    p {
      font-size: 25px;
      font-weight: 600;
      color: #AFB1B4;
    }
  }
}